import React from "react";
// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
import Slide from "@material-ui/core/Slide";
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";
// core components
import Button from "components/CustomButtons/Button.jsx";
import agbStyles from "assets/jss/material-kit-react/views/componentsSections/agbStyles.jsx";

function Transition(props) {
  return <Slide direction="down" {...props} />;
}

class SectionAGB extends React.Component {
  anchorElLeft = null;
  anchorElTop = null;
  anchorElBottom = null;
  anchorElRight = null;
  constructor(props) {
    super(props);
    this.state = {
      classicModal: false,
      openLeft: false,
      openTop: false,
      openBottom: false,
      openRight: false
    };
  }
  handleClickOpen(modal) {
    var x = [];
    x[modal] = true;
    this.setState(x);
  }
  handleClose(modal) {
    var x = [];
    x[modal] = false;
    this.setState(x);
  }
  handleClosePopover(state) {
    this.setState({
      [state]: false
    });
  }
  handleClickButton(state) {
    this.setState({
      [state]: true
    });
  }
  render() {
    const { classes } = this.props;
    return (
          <div>
                  <div
                    className={classes.block}
                    onClick={() => this.handleClickOpen("classicModal")}
                  >
                    AGB
                  </div>
                  <Dialog
                    classes={{
                      root: classes.center,
                      paper: classes.modal
                    }}
                    open={this.state.classicModal}
                    TransitionComponent={Transition}
                    keepMounted
                    onClose={() => this.handleClose("classicModal")}
                    aria-labelledby="classic-modal-slide-title"
                    aria-describedby="classic-modal-slide-description"
                  >
                    <DialogTitle
                      id="classic-modal-slide-title"
                      disableTypography
                      className={classes.modalHeader}
                    >
                      <h4 className={classes.modalTitle}>AGB</h4>
                    </DialogTitle>
                    <DialogContent
                      id="classic-modal-slide-description"
                      className={classes.modalBody}
                    >
                      
                        <h3>1. Anerkennung der Lieferungs- und Zahlungsbedingungen</h3>
<p>Fr die Durchfhrung unserer Lieferungen und Leistungen fr Motoren, Baugruppen oder Einzelteile (nachstehend "Vertragsgegenstand") gelten die nachstehenden Bedingungen. Andere oder entgegenstehende Bedingungen, z.B. Einkaufsbedingungen des Auftraggebers, gelten auch dann nicht, wenn wir ihnen nicht ausdrcklich widersprechen, es sei denn, wir htten ihnen schriftlich zugestimmt.</p>


<h3>2. Angebote - Kostenvoranschlge</h3>
<p>Sofern der Kunde Vollkaufmann ist, sind Angebote freibleibend, soweit nichts anderes schriftlich vereinbart ist. Unter der gleichen Voraussetzung, dass der Kunde Vollkaufmann ist, werden die zwecks Abgabe eines Kostenvoranschlags gemachten Leistungen und Lieferungen besonderer Art, wie insbesondere Reisen und Demontagearbeiten, dem Auftraggeber gesondert berechnet, und zwar auch dann, wenn es nicht oder nur in abgenderter Form zur Ausfhrung von Instandsetzungsarbeiten kommt. Wnscht der Kunde, gleichgltig ob Vollkaufmann oder nicht, einen verbindlichen Kostenvoranschlag, so wird dieser schriftlich erstellt. Darin werden die jeweiligen Arbeiten und Teile bzw. Liefergegenstnde im Einzelnen aufgelistet und mit dem jeweiligen Preis versehen. Der Auftragnehmer ist an den erstellten verbindlichen Kostenvoranschlag bis zum Ablauf von 21 Tagen nach seiner Abgabe gebunden. Bei unverbindlichen Kostenvoranschlgen gelten Abweichungen von + 10 % als statthaft. Zu weitergehenden berschreitungen holt der Auftragnehmer unverzglich vor Durchfhrung weiterer Arbeiten die Zustimmung des Auftraggebers ein. Dem Auftraggeber steht jedoch in diesem Falle ein Kndigungsrecht zu. Wenn dies im Einzelfall vereinbart ist, kann der Auftragnehmer dem Auftraggeber fr die Erstellung eines Kostenvoranschlages erbrachte Leistungen berechnen. Wenn jedoch aufgrund des Kostenvoranschlages ein Auftrag erteilt wird, werden fr den Kostenvoranschlag berechnete Betrge mit der Rechnung fr den Auftrag verrechnet. Preise im Kostenvoranschlag werden jeweils netto angegeben, im nichtkaufmnnischen Verkehr zuzglich gesondert ausgewiesener Mehrwertsteuer.</p>


<h3>3. Auftrge fr Instandsetzungen/Reparaturen</h3>
<p>3.1 Der Umfang der jeweiligen Instandsetzungsarbeiten/Reparaturen fr den Vertragsgegenstand ist vom Auftraggeber festzulegen. Sofern dies nicht mglich ist, legt der Auftragnehmer den Umfang der durchzufhrenden Instandsetzungsarbeiten nach Rcksprache mit dem Auftraggeber fest. In einem Auftragsschein oder in einem Besttigungsschreiben werden die vereinbarten bzw. mit dem Auftragnehmer abgestimmten zu erbringenden Leistungen bezeichnet. Der voraussichtliche oder der verbindliche Liefertermin wird angegeben. Stellt sich whrend der Bearbeitung, aber bei Auftragsannahme nicht erkennbar, heraus, dass die Instandsetzung wegen der Mngel des Vertragsgegenstandes unmglich ist, so ist der Auftragnehmer berechtigt, die bis zu dieser Feststellung geleisteten Arbeiten dem Auftraggeber in Rechnung zu stellen. Soweit sich whrend der Bearbeitung, aber bei Auftragsannahme nicht erkennbar, herausstellt, dass die Durchfhrung der Instandsetzungsarbeiten unwirtschaftlich ist, wird der Auftragnehmer den Auftraggeber unverzglich hiervon verstndigen, um eine definitive Entscheidung des Auftraggebers herbeizufhren. Entscheidet sich der Auftraggeber dazu, den Auftrag wegen seiner Unwirtschaftlichkeit nicht durchfhren zu lassen, so hat der Auftragnehmer Anspruch auf Abgeltung der bis zu diesem Zeitpunkt geleisteten Arbeiten, einschlielich eines angemessenen Gewinns. Der Auftragnehmer haftet nicht fr Fehler, die sich aus Unterlagen, Zeichnungen, Mustern usw. sowie aus Angaben des Auftraggebers ergeben, soweit ihm nicht zuzumuten ist, diese zu erkennen.</p>


<h3>4. Kauf/Tausch</h3>
<p>4.1 Gegenstand der Verpflichtung des Auftragnehmers kann auch die Lieferung eines generalberholten Vertragsgegenstandes, ggf. gegen bergabe eines entsprechenden alten Motors, einer Baugruppe oder eines Einzelteils gleicher Type sein. Abweichungen in der Ausfhrung sind dem Auftragnehmer gestattet, soweit dies dem Auftraggeber zumutbar ist. Vertragsgegenstnde des Auftraggebers, die dieser dem Auftragnehmer zum Einbau oder im Wege des Tausches berlsst, drfen keine Mngel oder sonstigen Fehler aufweisen, die nicht auf natrliche Abnutzung zurckzufhren sind. Insbesondere muss der anzuliefernde Vertragsgegenstand frei von geschweiten oder nichtgeschweiten Brchen und Rissen sein.<br />

4.2 Fr die im Falle des Tausches eines Vertragsgegenstandes zu leistende Entschdigung gelten die jeweiligen gesonderten Vereinbarungen.</p>


<h3>5. Preise und Zahlungen</h3>
<p>5.1 Alle Preise verstehen sich fr Lieferungen und Leistungen ab Betrieb des Auftragnehmers; es gelten die jeweiligen Listenpreise zzgl. der gesetzlichen Mehrwertsteuer.<br />

5.2 Die jeweiligen Preise gelten ausschlielich Porto, Fracht und Verpackung. Soweit die Verpackung vom Auftragnehmer beigestellt wird, werden die Selbstkosten berechnet. Beanstandungen oder Reklamationen der erteilten Rechnungen sollen, sofern sich nicht aus den Umstnden etwas anderes ergibt, innerhalb von acht Tagen nach Aushndigung der jeweiligen Rechnung erfolgen.<br />

5.3 Die jeweilige Mehrwertsteuer wird in gesetzlicher Hhe hinzugerechnet. Im nichtkaufmnnischen Bereich wird der Preis einschlielich gesondert ausgewiesener Mehrwertsteuer angegeben.<br />

5.4 Fr Vertragsgegenstnde, die im Tausch geliefert werden, ist der vereinbarte Preis davon abhngig, dass diese Hauptteile instandsetzungsfhig sind; nicht mehr instandsetzungsfhige Teile werden nachberechnet. Der Auftraggeber hat in diesem Fall einen Anspruch auf Rckforderung der Teile.<br />

5.5 Zahlungen sind Zug um Zug nach Erhalt der Rechnung - netto - zu leisten, soweit nichts anderes schriftlich vereinbart ist. Ein Abzug von Skonto ist unzulssig. Schecks und Wechsel werden nur nach vorheriger Vereinbarung - zahlungshalber - entgegengenommen, vorbehaltlich rechtzeitiger und ordnungsgemer Gutschrift. Anfallende Inkasso- und Diskontspesen werden weiterberechnet.<br />

5.6 Aufrechnungen sind nur statthaft, sofern die Gegenforderung rechtskrftig festgestellt, anerkannt oder unbestritten ist. Ein Zurckbehaltungsrecht steht darber hinaus dem Auftraggeber nur insoweit zu, als der Grund des Zurckbehaltungsrechts auf dem gleichen Vertragsverhltnis beruht.<br />

5.7 Bei umfangreichem Materialaufwand und langfristigen Arbeiten kann eine angemessene Vorauszahlung verlangt werden, um die Vorfinanzierung des Auftragnehmers zu gewhrleisten.</p>


<h3>6. Fertigstellung/Lieferzeit</h3>
<p>Es gilt, sofern verbindlich vereinbart, die jeweils angegebene Fertigstellungs- bzw. Lieferzeit. Soweit die rechtzeitige Lieferung/Leistung ein Fixgeschft ist, bleiben die gesetzlichen Ansprche unberhrt; dies gilt auch dann, wenn als Folge des Verzugs des Auftragnehmers die weitere Erfllung der Lieferung/Leistung fr den Auftraggeber ohne Interesse ist. ndert oder erweitert der Auftraggeber den ursprnglich vereinbarten Arbeitsumfang und verzgert sich die Fertigstellung bzw. Lieferung dadurch, haftet der Auftragnehmer dafr nicht. Er nennt dem Auftraggeber unter Angabe der Grnde jedoch unverzglich einen neuen Fertigstellungs- bzw. Liefertermin. Liegt die Ursache der Nichteinhaltung des Termins in hherer Gewalt oder in Betriebsstrungen, auch in solchen von Vorlieferanten oder Subunternehmern, die der Auftragnehmer nicht verschuldet hat, besteht aufgrund hierdurch bedingter Verzgerungen keine Schadenersatzverpflichtung des Auftragnehmers. Er unterrichtet den Auftraggeber jedoch unverzglich. Die Fertigstellungs- bzw. Lieferzeit verlngert sich ggf. um die Zeit, die der Auftraggeber mit der Anlieferung von ihm beizustellender notwendiger Teile in Rckstand ist. Der Auftragnehmer ist dabei berechtigt, den Vertrag nach fruchtloser Nachfristsetzung zu kndigen.</p>


<h3>7. Abnahme</h3>
<p>Die Abnahme findet im Betrieb des Auftragnehmers statt, soweit nichts anderes vereinbart ist. Der Auftraggeber kommt mit der Abnahme in Verzug, wenn er innerhalb von zwei Wochen, nachdem ihm die Fertigstellung des Vertragsgegenstandes gemeldet oder die endgltige Rechnung ausgehndigt worden ist, diesen gegen Begleichung der flligen Rechnung nicht abholt.</p>


<h3>8. Lieferung</h3>
<p>8.1 Die Lieferung des Vertragsgegenstandes erfolgt stets auf Rechnung und Gefahr des Auftraggebers, und zwar ab Betrieb des Auftragnehmers, soweit nicht schriftlich anderes vereinbart ist. Fr Leistungen gilt Entsprechendes.<br />

8.2 Wnscht der Auftraggeber Zustellung, so erfolgt diese auf Rechnung und Gefahr des Auftraggebers.</p>


<h3>9. Eigentumsvorbehalt</h3>
<p>9.1 Der gelieferte Vertragsgegenstand bleibt bis zur vollstndigen Bezahlung aller zwischen Auftraggeber und Auftragnehmer (bereits) entstandenen Forderungen Eigentum des Auftragnehmers. Im kaufmnnischen Verkehr gilt des weiteren, dass die Eigentumsvorbehaltssicherung sich auf den jeweiligen Saldo bezieht, sofern zwischen Auftraggeber und Auftragnehmer eine Kontokorrentvereinbarung besteht. In diesem Fall erstreckt sich der Eigentumsvorbehalt auch auf den kausalen Saldo, sobald der Auftraggeber in Konkurs fllt.<br />

9.2 Der Auftraggeber erklrt sich, wenn er den gelieferten Vertragsgegenstand weiterbearbeitet, damit einverstanden, dass die Bearbeitung stets fr den Auftragnehmer erfolgt. Der Auftragnehmer erwirbt Eigentum an dem zu bearbeitenden Vertragsgegenstand.<br />

9.3 Sofern der Auftraggeber Hndler ist, ist er zur Weiterveruerung des Vertragsgegenstandes im normalen Geschftsgang berechtigt. Fr diesen Fall tritt jedoch der Auftraggeber die ihm gegenber seinen Abnehmern zustehenden Forderungen schon jetzt an den Auftragnehmer ab; zur Einziehung dieser Forderungen bleibt der Auftraggeber berechtigt, solange er nicht gegenber dem Auftragnehmer in Zahlungsverzug geraten ist. Soweit dies der Fall ist, ist der Auftragnehmer berechtigt, die Weiterveruerungs- und Einziehungsbefugnis fr den Vertragsgegenstand schriftlich zu widerrufen. In diesem Fall ist der Auftraggeber verpflichtet, dem Auftragnehmer alle Informationen, Dokumentationen und sonstigen Unterlagen zu berlassen, aus denen sich ergibt, gegen welche Abnehmer dem Auftragnehmer Forderungen aufgrund des verlngerten Eigentumsvorbehalts zustehen, damit der Auftragnehmer in der Lage ist, diese gegenber den Abnehmern unmittelbar geltend zu machen.<br />

9.4 Bei Verbindungen oder Vermischungen eines Vertragsgegenstandes entsteht Miteigentum des Auftragnehmers, sofern nicht eine Sache als Hauptsache anzusehen ist. Soweit letzteres der Fall ist, erklrt sich der Auftraggeber schon jetzt damit einverstanden, Sicherungseigentum zugunsten des Auftragnehmers - bezogen auf die Hauptsache - zu vereinbaren. Dieses verwahrt der Auftraggeber unentgeltlich fr den Auftragnehmer.fr den Auftragnehmer.<br />

9.5 Die Sicherungsbereignung gem Ziff. 9.4 sowie die Sicherungsabtretung gem 9.3 gelten jeweils in Hhe des Fakturaendbetrages, wie sie zwischen Auftraggeber und Auftragnehmer vereinbart wurde; der Fakturaendbetrag versteht sich einschlielich der gesetzlichen Mehrwertsteuer.<br />

9.6 Wird der unter Vorbehalt stehende Vertragsgegenstand zusammen mit anderen Waren weiterveruert, so gilt die Bestimmung gem Ziff. 9.3. und 9.4. sinngem.bersteigen die dem Auftragnehmer nach den vorstehenden Bestimmungen zustehenden Sicherheiten die zu sichernden Forderungen um mehr als 20%, so ist der Auftragnehmer verpflichtet, auf Verlangen des Auftraggebers berschieende Sicherheiten nach Wahl des Auftragnehmers freizugeben.</p>


<h3>10. Pfandrecht - Verwertung - Standgebhr</h3>
<p>10.1 Dem Auftragnehmer steht ein gesetzliches Pfandrecht an allen Gegenstnden des Auftraggebers zu, die mit Wissen und Wollen des Auftraggebers vom Auftragnehmer bearbeitet werden. Das Pfandrecht erstreckt sich auf alle Forderungen des Auftragnehmers, wie sie der Eigentumsvorbehaltsicherung gem Ziff. 9.1 entsprechen.<br />

10.2 Kommt der Auftraggeber mit der Zahlung fr einen lngeren Zeitpunkt als zwei Monate in Verzug, so steht dem Auftragnehmer das Recht zu, nach vorheriger schriftlicher Ankndigung und nach Ablauf einer weiteren Wartezeit von vier Wochen den Vertragsgegenstand durch Versteigerung und bei Vorliegen von Marktpreisen durch freihndigen Verkauf bestmglich zu verwerten. Ein etwaiger Verwertungserls steht dem Auftraggeber zu; der Auftragnehmer ist berechtigt, neben seiner Hauptforderung und den angelaufenen Zinsen auch die durch die Verwertung verursachten Kosten in Abzug zu bringen. Ist der Auftragnehmer aus betrieblichen Grnden zur Verwahrung der Pfandsache nicht in der Lage, kann er Ersatz der ihm durch eine anderweitige Lagerung entstandenen Kosten verlangen. Auch bei Verwahrung im eigenen Betrieb entstehende Verwahrkosten werden zu marktblichen Preisen dem Auftraggeber in Rechnung gestellt.</p>


<h3>11. Sachmangelhaftung bei Instandsetzung/Reparatur</h3>
<p>11.1 Der Auftragnehmer leistet Gewhr fr einwandfreie Arbeit und die Verwendung einwandfreien, funktionstchtigen Materials. Die Sachmangelansprche des Auftraggebers verjhren innerhalb eines Jahres ab Abnahme des Gegenstandes. Nimmt der Auftraggeber die Sache in Kenntnis eines Sachmangels ab, stehen ihm die Sachmangelansprche in unten beschriebenen Umfang nur zu, wenn er sich diese bei der Abnahme vorbehlt.<br />

11.2 Sofern der Auftraggeber Vollkaufmann ist, bleiben die gesetzlichen Untersuchungs- und Rgepflichten gem  377, 378, 381 Abs. 2 HGB unberhrt.<br />

11.3 Im Fall der Sachmangelhaftung ist der Auftragnehmer berechtigt und verpflichtet, Mangelbeseitigung auf eigene Kosten durchzufhren. Er ist auch berechtigt, eine Ersatzlieferung zu ttigen. Ist der Auftragnehmer nicht bereit oder nicht in der Lage, insbesondere verzgert sich die durchzufhrende Mangelbeseitigung/Ersatzlieferung ber angemessene Fristen hinaus oder schlgt sie aus sonstigen Grnden fehl, so ist der Auftraggeber berechtigt, vom Vertrag zurckzutreten (Rcktritt) oder entsprechende Herabsetzung der Vergtung (Minderung) zu begehren.<br />

11.4 Mangelbeseitigungsansprche hat der Auftraggeber beim Auftragnehmer geltend zu machen. Der Auftragnehmer anerkennt Mangelbeseitigungsarbeiten, die Dritte ausfhren, nur dann, wenn er im vorhinein hiermit ausdrcklich einverstanden ist und wenn der Auftragnehmer mit der Durchfhrung der Sachmangelbeseitung in Verzug geraten ist bzw. wenn ein uerst dringendes Erfordernis, insbesonders Betriebsunfhigkeit des Gegenstandes an einem mehr als 50 km vom Betriebsort des Auftraggebers entfernten Ort, besteht.<br />

11.5 Die Bearbeitung von einzelnen Vertragsgegenstnden geschieht nur im vereinbarten Umfang. Die Sachmangelhaftung erstreckt sich nicht auf eine darber hinausgehende Funktionstchtigkeit einzelner Teile. Diese sind nicht Gegenstand der Sachmangelhaftung des Auftragnehmers, soweit nichts anderes schriftlich vereinbart ist. Insbesonders wird ohne gesonderte schriftliche Vereinbarung keine Haltbarkeitsgarantie bernommen.<br />

11.6 Die Schadensersatzhaftung des Auftragnehmers ergibt sich aus der Regelung gem Ziff. 13.<br />

11.7 Soweit der Auftragnehmer ein Tuning von Vertragsgegenstnden oder eine Bearbeitung von Oldtimer-Vertragsgegenstnden bernimmt, beschrnkt sich seine Sachmangelhaftung auf die ordnungsgeme Ausfhrung dieser Arbeiten. Ein werkvertraglicher Erfolg ist nur dann geschuldet, wenn dies schriftlich zwischen Auftraggeber und Auftragnehmer vereinbart ist.<br />

11.8 Richtet sich der Auftrag auf die Lieferung herzustellender oder zu erzeugender beweglicher Sachen, und ist der Auftraggeber Unternehmer, der den Vertrag in Ausbung seiner selbstndigen beruflichen oder gewerblichen Ttigkeit abschliet, oder ist er eine juristische Person des ffentlichen Rechtes oder ein ffentlich  rechtliches Sondervermgen, verjhren Sachmangelansprche in einem Jahr ab Lieferung. Ist der Auftraggeber Verbraucher, gelten in diesem Fall die gesetzlichen Bestimmungen.<br />

11.9 Wenn ein Mangel nach nicht vom Auftragnehmer durchgefhrter Montage / Einbau auftritt, haftet der Auftragnehmer im Rahmen der Sachmangelhaftung nur, wenn Montage oder Einbau der vom Auftragnehmer zuvor bearbeiteten oder verkauften Sache fachkundig und fachgerecht, insbesondere nach Magabe und Vorschrift des Herstellers, erfolgte. Die Fachkundigkeit und Fachgerechtigkeit der Montage bzw. des Einbaus mu der Auftraggeber beweisen.</p>


<h3>12. Sachmangelhaftung bei Kauf/Tausch gebrauchter Gegenstnde</h3>
<p>Sachmangelansprche des Kufers verjhren innerhalb eines Jahres ab Lieferung des Kaufgegenstandes an den Kufer. Bei arglistig verschwiegenen Mngeln bleiben weitergehende Ansprche unberhrt. Fr die Mangelbeseitigungsabwicklung gilt das gleiche wie unter Ziffer 11.4..</p>


<h3>13. Sonstige Haftung</h3>
<p>13.1 Der Auftragnehmer haftet nicht fr Schden, welche nicht an dem Vertragsgegenstand unmittelbar entstanden sind. Insbesondere haftet der Auftragnehmer nicht fr alle Folgeschden oder entgangenen Gewinn.<br />

13.2 Dies gilt nicht, soweit die Ansprche des Auftraggebers auf dem Fehlen einer zugesicherten Eigenschaft beruhen. Gleiches gilt, soweit die Ansprche des Auftraggebers auf einer vorstzlichen oder grob fahrlssigen Vertragsverletzung des Auftragnehmers beruhen.<br />

13.3 Die Schadensersatzhaftung des Auftragnehmers ist auf den vorhersehbaren Schaden begrenzt, soweit der Auftragnehmer eine wesentliche Vertragspflicht infolge einfacher Fahrlssigkeit verletzt. Im brigen ist die Haftung fr einfache Fahrlssigkeit ausgeschlossen, soweit nicht Schden aus der Verletzung des Lebens, des Krpers oder der Gesundheit betroffen sind.<br />

13.4 Eine Haftung gem  1 Produkthaftungsgesetz bleibt unberhrt.</p>


<h3>14. Gerichtsstand - Erfllungsort</h3>
<p>14.1 Sofern nichts anderes schriftlich vereinbart, ist Erfllungsort der Sitz des Auftragnehmers.<br />

14.2 Ausschlielicher Gerichtsstand fr alle sich aus dem Zusammenhang mit diesem Vertrag gegebenen Streitigkeiten ist der Sitz des Auftragnehmers, soweit der Auftraggeber Vollkaufmann ist. Im brigen bleibt es bei den gesetzlichen Regelungen.<br />

14.3 Der Vertrag unterliegt dem Recht der Bundesrepublik Deutschland; die Geltung des UN-Kaufrechts ist abbedungen.</p>
                    </DialogContent>
                    <DialogActions className={classes.modalFooter}>
                      <Button
                        onClick={() => this.handleClose("classicModal")}
                        color="danger"
                        simple
                      >
                        Schlieen
                      </Button>
                    </DialogActions>
                  </Dialog>
                  </div>
    );
  }
}

export default withStyles(agbStyles)(SectionAGB);